html {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  line-height: 1;
  color: #212529; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
small {
  font-size: 16px;
  line-height: 1;
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

hr {
  margin: 0 !important;
  border: 0.5px solid #dee2e6; }

h1 {
  font-size: 3rem !important; }

h2 {
  font-size: 2.25rem !important; }

h3 {
  font-size: 1.5rem !important; }

h4 {
  font-size: 1.25rem !important; }

h5 {
  font-size: 1rem !important; }

h6 {
  font-size: 0.875rem !important; }

.btn {
  background: blue; }

.Content h1,
.Content h2,
.Content h3,
.Content h4,
.Content h5,
.Content h6 {
  font-family: "Times New Roman", Times, Baskerville, Georgia, serif;
  margin-bottom: 1rem; }
  .Content h1:first-of-type,
  .Content h2:first-of-type,
  .Content h3:first-of-type,
  .Content h4:first-of-type,
  .Content h5:first-of-type,
  .Content h6:first-of-type {
    margin-top: 2rem; }
  .Content h1:last-of-type,
  .Content h2:last-of-type,
  .Content h3:last-of-type,
  .Content h4:last-of-type,
  .Content h5:last-of-type,
  .Content h6:last-of-type {
    margin-bottom: 2rem; }

.Content p {
  line-height: 1.5rem;
  margin-bottom: 24px;
  margin-top: 24px; }

.Content img,
.Content iframe {
  width: auto;
  height: auto;
  max-width: 100%;
  margin-bottom: 2rem; }

.tooltip {
  position: relative; }
  .tooltip:after {
    position: absolute;
    z-index: 1000000;
    display: none;
    padding: 0.25em 0.5em;
    color: white;
    text-align: center;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: break-word;
    white-space: pre;
    pointer-events: none;
    content: attr(aria-label);
    background-color: #000;
    left: 20%;
    bottom: 100%;
    margin-bottom: 6px;
    font-size: 14px;
    border-radius: 0.35em; }
  .tooltip:before {
    position: absolute;
    z-index: 1000001;
    display: none;
    width: 0;
    height: 0;
    color: #000;
    pointer-events: none;
    content: "";
    border: 6px solid transparent;
    top: -7px;
    bottom: auto;
    left: auto;
    border-top-color: black; }
  .tooltip:hover:before, .tooltip:hover:after {
    display: flex !important; }
